@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  font-family: 'Roboto Condensed', sans-serif;
}
*{
  font-family: 'Roboto Condensed', sans-serif;
  padding: 0;
  margin: 0;
  list-style: none;
}
:root{
  --primary-white-color:#fff;
  --primary-orange:#f9ab00;
  --primary-dark: rgba(52,58,64);
  --primary-red:#ef4035;
  --primary-hover-red:#f33501;
  --primary-light-grey:#f4f4f4;
  --primary-dark-grey:#666666;
  --primary-transparent-black:rgba(0,0,0,.7);
  --primary-black:#000;
  --primary-grey:#ccd1d9;
}

/*************************Navbar************************/

.header{
  position: fixed;
  background: rgba(52,58,64);
  background: var(--primary-dark);
  top: 0;
  z-index: 50;
  width: 100%;
  color: #fff;
  color: var(--primary-white-color);
  height: 100px;
}
.header .hamburger{
  display: none;
}

.header .navbar ul{
  padding-left: 0;
  margin-bottom: 0;
  
}
.header .navbar .indicator-area{
  width: 100%;
}
.header .navbar .indicator{
  position: relative;
  background: #f9ab00;
  background: var(--primary-orange);
  left: 0;
  width: 20%;
  height: 2px;
  transition: .4s ease-in-out;
}
.header .navbar .singleLi{
  width: 100px;
  text-align: center;
  cursor: pointer;
}


.header .navbar .singleLi a{

  display: inline-block;
  width: 100%;
  padding: 10px 0;
}
@media only screen and (max-width:768px){
  .header{
    padding: 0 50px;
  }
  .header .navbar{
    background: rgba(52,58,64);
    background: var(--primary-dark);
    position: absolute;
    left: 0;
    right: 0;
    top: 80px;
    z-index: 5;
    transition: .3s ease-in-out;
    background: transparent;
    visibility: hidden;
  }
  .header .navbar.open{
    background: rgba(52,58,64);
    background: var(--primary-dark);
    visibility: visible;
  }
  .header .navbar ul{
    -webkit-flex-direction: column;
            flex-direction: column;
    color: transparent;
    transition: .1s ease-in-out;
  }
  .header .navbar.open ul{
    color: #fff;
    color: var(--primary-white-color);
  }
  .header .hamburger{
    display: block;
    cursor: pointer;
  }
  .header .navbar .indicator-area{
    display: none;
  }
  .header .navbar .singleLi{
    border-bottom: 2px solid transparent;
    transition: .4s ease-in-out;
  }
  .header .navbar.open .singleLi:hover{
    border-bottom: 2px solid #f9ab00;
    border-bottom: 2px solid var(--primary-orange);
  }
  .header .navbar.open .singleLi + .singleLi{
    margin-top: 20px;
  }
}





/**************************Header************************/

.header-wrapper{
  position: relative;
  background: url(/portfolio-react-bootstrap/static/media/wall-and-laptop-background.f9e274f4.jpg) no-repeat;
  background-size:cover ;
  background-position: center;
  height: 100vh;
}
.main-info{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  position:absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
