/**************************Header Main Offer Btn************************/

.btn-main-offer{
  border: 1px solid #ef4035;
  border: 1px solid var(--primary-red);
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  text-decoration: none;
  color: #fff;
  color: var(--primary-white-color);
  margin: 2rem; 
  transition: .3s;
}
.btn-main-offer:hover{
