/************About Me************/

/* .about-me{
  height: 46.875rem;
} */
.selfie{
  width: 16rem;
  height: 16rem;
  border-radius: 50%;
  border: 1px solid #f9ab00;
  border: 1px solid var(--primary-orange);
  overflow: hidden;
  padding: .3rem;
}
.steven-img{
  width: 100%;
  object-fit: cover;
  -webkit-transform: scale(.9);
          transform: scale(.9);
}
.steven-content{
  border: 1px solid #000;
}
.about-me-title{
  color: #f9ab00;
  color: var(--primary-orange);
  text-transform: uppercase;
}


/*************Services*************/

.services{
  background: #f4f4f4;
  background: var(--primary-light-grey);
}
.services h1{
  color: rgba(52,58,64);
  color: var(--primary-dark);
  text-transform: uppercase;
}

.circle{
  background: #f9ab00;
  background: var(--primary-orange);
  width: 3.125rem;
  height: 3.125rem;
  border-radius: 50%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin: auto;
  margin-bottom: 1.25rem;
  color: #fff;
  color: var(--primary-white-color);
  font-size: 0.75rem;
  transition: .3s ease-in-out;
}

.box{
  height: 18rem;
  border-bottom: 0.375rem solid #f9ab00;
  border-bottom: 0.375rem solid var(--primary-orange);
  background: #fff;
  padding-top: 1.563rem;
  transition: .3s ease-in-out;
}

.outside-box:hover .box{
  background: #f9ab00;
  background: var(--primary-orange);
  border-bottom-color: rgba(52,58,64);
  border-bottom-color: var(--primary-dark) ;
  
}
.outside-box:hover .circle{
  color: rgba(52,58,64);
  color: var(--primary-dark);
  background: #fff;
  background: var(--primary-white-color);
}
.outside-box p{
  transition: .3s ease-in-out;
}
.outside-box:hover p{
  color: #fff;
  color: var(--primary-white-color);
}
@media screen and (max-width:768px){
  .outside-box{
    margin-bottom: 1.563rem;
  }
}
@media screen and (max-width:992px){
  .outside-box{
    margin-bottom: 1.563rem;
  }
}

/**************Experiences************/
.experiences h1{
  color: #f9ab00;
  color: var(--primary-orange);
}
.experiences-info{
  position: relative;
  padding-top: 3.125rem;
}
.experiences-info::before{
  content: '';
  display: block;
  position: absolute;
  width: 0.063rem;
  height: 100%;
  background: #bbb;
  top: 0;
  bottom: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
.item-left, .item-right{
  position: relative;
  
}
.item-left h3, .item-right h3{
  color: #ef4035;
  color: var(--primary-red);
  font-weight: 900;
  
}
.item-left-mark, .item-right-mark{
  position: absolute;
  width: 1.25rem;
  height: 1.25rem;
  border-radius: 50%;
  background: #000;
}
.item-left-mark{
  right: -0.625rem;
  top: 0.313rem;
}
.item-right-mark{
  left: -0.625rem;
  top: 0.313rem;
}

.item-left-mark::before, .item-right-mark::before{
  content: '';
  width: 0.5rem;
  height: 0.5rem;
  position: absolute;
  background: #f9ab00;
  background: var(--primary-orange);
  border-radius: 50%;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
}
@media screen and (max-width:992px){
  .experiences-info{
    margin: 0 0.625rem !important;
   
  }
  .experiences-info::before{
    left: 0;
  }
  .item-left-mark{
    left: -0.625rem;
  }
}

/**********Portfolio***********/
.portfolio{
  background: #f4f4f4;
  background: var(--primary-light-grey);
}
.img-container{
  overflow: hidden;
  height: 8.75rem;

}
.img-container img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.space{
  border: 1px solid #f9ab00;
  border: 1px solid var(--primary-orange);
  position: relative;
  cursor: pointer;
}
.space img{
  -webkit-transform: scale(1);
          transform: scale(1);
  transition: .3s ease-in-out;
}
.space::before{
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: rgba(0, 0, 0, 0);
  z-index: 2;
  transition: .3s ease-in-out;
}
.space:hover::before{
  background: rgba(0, 0, 0, .6);
}
.space:hover img{
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.fa-search-plus{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  z-index: 3;
  color: rgba(255, 255, 255, 0);
  transition: .3s ease-in-out;
  font-size: 1.563rem;
}
.space:hover .fa-search-plus{
  color: #f9ab00;
  color: var(--primary-orange);
}
@media screen and (max-width:768px){
  .img-container{
    height: 15.625rem;
  }
}
