  margin-top: 3% !important;
  width: 100%;
}

/********************Contact-Me**********************/
.contact-me{
  background: rgba(52,58,64);
  background: var(--primary-dark);
  padding: 3rem 0;
}

.contact-me h1{
  color: #f9ab00;
  color: var(--primary-orange);
  padding: 1.3rem 0;
  text-transform: uppercase;
}

.contact-me p{
  color: #f4f4f4;
  color: var(--primary-light-grey);
  font-size: 1.2rem;
}
.contact-me input, .contact-me textarea{
  width: 100%;
  box-sizing: border-box;
  background: none;
  resize: none;
  border: 0;
  border-bottom: 0.125rem solid #666666;
  border-bottom: 0.125rem solid var(--primary-dark-grey);
  border-radius: 0;
  color: #ccd1d9;
  color: var(--primary-grey);
  outline: 0 !important;
  box-shadow: none !important;
  top: 0;
  margin-bottom: 1rem;
  position: relative;
 
}

.contact-me input:focus, .contact-me textarea:focus{
  background: none;
  color: #f9ab00;
  color: var(--primary-orange);
  border-bottom: 0.125rem solid #666666;
  border-bottom: 0.125rem solid var(--primary-dark-grey);
}

.contact-me textarea{
  height: 16.5rem !important; 
  width: 100% !important;
}

.contact-btn{
  margin: 1rem 0 0 0;
  background: none;
  width: 100%;
}

.line{
  position: relative;
  width: 0;
  height: 0.125rem;
  background: #f9ab00;
  background: var(--primary-orange);
  display: inline-block;
  bottom: 2.05rem;
}
textarea + .line{
  bottom: 2.1rem;
  margin: 0 auto;
}
input:focus + .line, textarea:focus + .line{
  width: 100%;
  transition: width .5s ease-in-out;
}
.input-area{
  height: 2.625rem;
}
.textarea-area{
  height: 16.563rem;
}
.error-message{
  display: inline-block;
  width: 100%;
  margin-bottom: 0.625rem;
  text-align: left;
  font-size: 0.938rem;
}

/*********************Footer***********************/

.footer{
  background: #000;
  color: #666666;
  color: var(--primary-dark-grey);
 
}

.footer .row .col{
  margin-top: 15px;
}


.social-media{
  border-radius: 50%;
}

@media screen and (max-width:992px){
  .social-media-container{
    -webkit-align-items: flex-start;
            align-items: flex-start;
  }
  .footer .row .col{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
  }
}

